<template>
    <div class="sales-board">
        <div class="sales-board-intro">
            <h2></h2>
            <p></p>
        </div>

        <div class="sales-board-form">
            <div class="sales-board-line">
                <div class="sales-board-line-left"></div>
                <div class="sales-board-line-right">
                    <base-chooser :chooses="buyTypes" @on-choose="salesSelecteOnChoose"></base-chooser>
                </div>
            </div>

            <div class="sales-board-line">
                <div class="sales-board-line-left"></div>
                <div class="sales-board-line-right">
                    <base-chooser-mult
                        :chooses="versionList"
                        @on-choose="onParamChange('versions', $event)"
                    ></base-chooser-mult>
                </div>
            </div>

            <div class="sales-board-line">
                <div class="sales-board-line-left"></div>
                <div class="sales-board-line-right">
                    <base-counter :max="999" @on-change="onParamChange('buyNum', $event)"></base-counter>
                </div>
            </div>

            <div class="sales-board-line">
                <div class="sales-board-line-left"></div>
                <div class="sales-board-line-right">
                    <base-selecte :selections="districts" @on-change="districtsSelecteOnChange"></base-selecte>
                </div>
            </div>

            <div class="sales-board-line">
                <div class="sales-board-line-left"></div>
                <div class="sales-board-line-right"></div>
            </div>

            <div class="sales-board-line">
                <div class="sales-board-line-left"></div>
                <div class="sales-board-line-right">500 </div>
            </div>

            <div class="sales-board-line">
                <div class="sales-board-line-left"></div>
                <div class="sales-board-line-right">{{ $store.state.inventory }} </div>
            </div>

            <div class="sales-board-line">
                <div class="sales-board-line-left">&nbsp;</div>
                <div class="sales-board-line-right">
                    <div class="button"></div>
                </div>
            </div>
        </div>

        <div class="sales-board-des">
            <h2></h2>
            <p></p>

            <table class="sales-board-table">
                <tbody>
                    <tr class="ui-table-row" v-for="item in salesDes" :key="item.text">
                        <td class="col-first">
                            <div class="intro-pic">
                                <label>{{ item.name }}</label>
                            </div>
                        </td>

                        <td v-for="text in item.items" :key="text">
                            <div class="intro-pic">
                                <label>{{ text }}</label>
                            </div>
                        </td>
                    </tr>
                </tbody>
            </table>
        </div>
    </div>
</template>

<script>
    import BaseSelecte from "@/components/base/BaseSelecte.vue";
    import BaseChooser from "@/components/base/BaseChooser.vue";
    import BaseChooserMult from "@/components/base/BaseChooserMult.vue";
    import BaseCounter from "@/components/base/BaseCounter.vue";

    export default {
        data() {
            return {
                salesDes: [
                    {
                        name: "",
                        items: [
                            "",
                            "",
                            "",
                            "",
                            "",
                            ""
                        ]
                    },
                    {
                        name: "",
                        items: [
                            "",
                            "",
                            "",
                            "",
                            "",
                            ""
                        ]
                    },
                    {
                        name: "",
                        items: [
                            "",
                            "",
                            "",
                            "",
                            "",
                            ""
                        ]
                    },
                    {
                        name: "",
                        items: [
                            "",
                            "",
                            "",
                            "",
                            "",
                            ""
                        ]
                    },
                    {
                        name: "",
                        items: [
                            "",
                            "",
                            "",
                            "",
                            "",
                            ""
                        ]
                    }
                ],
                buyTypes: [
                    {
                        label: "",
                        value: 0
                    },
                    {
                        label: "",
                        value: 1
                    },
                    {
                        label: "",
                        value: 2
                    }
                ],
                districts: [
                    {
                        label: "",
                        value: 0
                    },
                    {
                        label: "",
                        value: 1
                    },
                    {
                        label: "",
                        value: 2
                    },
                    {
                        label: "",
                        value: 3
                    },
                    {
                        label: "",
                        value: 4
                    },
                    {
                        label: "",
                        value: 5
                    }
                ],
                versionList: [
                    {
                        label: "",
                        value: 0
                    },
                    {
                        label: "",
                        value: 1
                    },
                    {
                        label: "",
                        value: 2
                    }
                ]
            };
        },
        created() {},
        mounted() {},
        methods: {
            districtsSelecteOnChange(data) {
                console.log("data: ", data);
            },
            salesSelecteOnChoose(data) {
                console.log("data: ", data);
            },
            onParamChange(attr, val) {
                console.log("attr: ", attr);
                console.log("val: ", val);
            }
        },
        components: {
            BaseSelecte,
            BaseChooser,
            BaseChooserMult,
            BaseCounter
        }
    };
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
</style>



